import React from 'react';
import Order from "./Order/Order";
import './App.css';

class App extends React.Component {
  constructor() {
    super();
    this.state = {
      loading: false,
      filteredOrder: [],
      orderData: [],
      error: false,
      names: {}
    }
    this.search = this.search.bind(this)
    this.sortByDeadlineAsc = this.sortByDeadlineAsc.bind(this)
    this.sortByDeadlineDesc = this.sortByDeadlineDesc.bind(this)
  }

  addNameToParent(name, order) {
    var names = this.state.names;
    if(name in this.state.names){
      names[name].push(order);
    }else{
      names[name] = [order];
    }
    this.setState({
      names: names
    });
  }

  search = (event) => {
    if (this.state.orderData == null) {
      return;
    }
    let ordersDuplicate = [...this.state.orderData]
    let search = event.target.value;
    if(search.length === 0){
      for(var i in ordersDuplicate){
        var order = ordersDuplicate[i]
        order.display = true;
      }
      this.setState({
        filteredOrder: this.state.orderData
      });
      return;
    }else{
      let searchArray = [];
      Object.entries(this.state.names).forEach(entry => {
        let key = entry[0];
        let value = entry[1];
        if (key.toLowerCase().indexOf(search.toLowerCase()) !== -1) {
          searchArray.push(...value)
        }
      });
      for(var k in ordersDuplicate){
        // eslint-disable-next-line
        var order = ordersDuplicate[k];
        order.display = false;
        for(var j in searchArray){
          var orderid = searchArray[j]
          if(order.id === orderid.id){
            order.display = true;
            break;
          }
        }
      }
      this.setState({
        filteredOrder: ordersDuplicate
      });
      return;
    }

  }

  sortByDeadlineAsc() {
    if (this.state.orderData == null) {
      return;
    }
    let ordersDuplicate = [...this.state.filteredOrder]
    ordersDuplicate = ordersDuplicate.sort((a, b) => (a.deadline - b.deadline))
    this.setState({
      filteredOrder: ordersDuplicate
    });
  }

  sortByDeadlineDesc() {
    if (this.state.orderData == null) {
      return;
    }
    let ordersDuplicate = [...this.state.filteredOrder]
    ordersDuplicate = ordersDuplicate.sort((a, b) => (b.deadline - a.deadline))
    this.setState({
      filteredOrder: ordersDuplicate
    });
  }

  componentDidMount() {
    this.setState({
      loading: true
    })
    fetch("https://www.hatchways.io/api/assessment/work_orders")
    .then(results => {
      if (!results.ok) throw new Error(results.status)
      else return results.json()
    })
    .then(data => {
      // eslint-disable-next-line
      Array.from(data.orders).map(order => {
        order.display = true;
        order.worker_name = '';
      });
      this.setState({
        orderData: data.orders,
        loading: false,
        filteredOrder: data.orders
      })
    })
    .catch((error) => {
      console.log('error: ' + error);
      this.setState({
        error: true
      })
    })
  }


  render(props) {
    if(this.state.loading){
      return (
        <div>
          <header>
            <h1 id="title">Order! Order!</h1>
            <input type="text" name="search" onChange={this.search} placeholder="Filter by order id..."/>
          </header>
          <div className="buttonContainer">
              <input type="button" onClick={this.sortByDeadlineAsc} value="Sort Ascending"/>
              <input type="button" onClick={this.sortByDeadlineDesc} value="Sort Descending"/>
          </div>
          <div className="mainDiv row">
            <h2>Loading...</h2>
          </div>
        </div>
      );
    }else{
      let workers_display = [];
      let workers_not_display = [];
      let tmp_list = [];
      for(var i = 0; i < this.state.filteredOrder.length; i += 1){
        var order = this.state.filteredOrder[i]
        if(order.display){
          tmp_list.push(<Order display={order.display} addWorkerName={this.addNameToParent.bind(this)} key={order.id} name={order.name} description={order.description} deadline={order.deadline} id={order.id} workerId={order.workerId}/>)
          if(tmp_list.length === 2){
            workers_display.push(<div key={i} className="mainDiv row">{tmp_list[0]}{tmp_list[1]}</div>);
            tmp_list = [];
          }
        }else{
          workers_not_display.push(<Order display={order.display} addWorkerName={this.addNameToParent.bind(this)} key={order.id} name={order.name} description={order.description} deadline={order.deadline} id={order.id} workerId={order.workerId}/>)
        }
      }
      if(tmp_list.length === 1){
        workers_display.push(<div key={i} className="mainDiv row">{tmp_list[0]}</div>);
        tmp_list = [];
      }

      return (
        <div>
          <header>
            <h1 id="title">Order! Order!</h1>
            <input type="text" name="search" onChange={this.search} placeholder="Filter by Name!"/>
          </header>
          <div className="buttonContainer">
              <input type="button" onClick={this.sortByDeadlineAsc} value="Sort Ascending"/>
              <input type="button" onClick={this.sortByDeadlineDesc} value="Sort Descending"/>
          </div>
          {workers_display}
        </div>
      );
    }

  }
}

export default App;
